<template>
  <div>
    <loading :active="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>購買時間</th>
          <th>Email</th>
          <th class="d-lg-table-cell d-none">購買款項</th>
          <th>應付金額</th>
          <th class="d-lg-table-cell d-none">是否付款</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="item in orders" :key="item.id">
          <td>{{ item.create_at }}</td>
          <td v-if="item.user">{{ item.user.email }}</td>
          <td class="d-lg-table-cell d-none">
            <ul class="list-unstyled">
              <li v-for="(order, i) in item.products" :key="i">
                {{ order.product.title }}:{{ order.qty }}
                {{ order.product.unit }}
              </li>
            </ul>
          </td>
          <td>{{ $filters.currency(item.total) }}</td>
          <td class="d-lg-table-cell d-none">
            <strong class="text-success" v-if="item.is_paid">已付款</strong>
            <span class="text-muted" v-else>尚未付款</span>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- 產品列表分頁 -->
    <pagination :paginations="pagination" @emitGetProducts="getOrders"></pagination>
  </div>
</template>

<script>
import pagination from '@/components/Pagination.vue';

export default {
  data() {
    return {
      orders: [],   // 用來儲存JSON資料
      pagination: {},  // 撈頁碼數
      isLoading: false   // 預設為停止loading狀態
    }
  },
  components: {
    pagination
  },
  methods: {
    getOrders(page = 1) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/orders?page=${page}`;  // 取得商品列表
      // 取得JSON數據時即執行loading狀態
      this.isLoading = true; 

      this.$http.get(api).then((response) => {
        // 取得JSON數據之後即停止loading狀態
        this.isLoading = false; 

        this.orders = response.data.orders;
        this.pagination = response.data.pagination;
      });
    }
  },
  created() {
    this.getOrders();
  }
}
</script>